import { useState } from "react";
import Image from "next/image";
import logo from "../Images/482548321_3851477701771516_6987305829381938546_n.png"; // Use your logo

export default function Login() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  
  const handleLogin = (e) => {
    e.preventDefault();
    // Handle login logic here
    console.log({ username, password });
  };

  return (
    <div className="bg-white text-black h-screen flex flex-col justify-between">
      {/* Header with Logo */}
      <div className="flex justify-center items-center p-6">
        <Image src={logo} width={200} height={200} alt="logo" />
      </div>

      {/* Login Form Section */}
      <div className="flex justify-center items-center w-full px-6 md:px-20 py-16">
        <div className="w-full max-w-sm bg-white p-8 rounded-lg shadow-lg border border-gray-200">
          <h2 className="text-3xl font-bold text-center mb-6">Login</h2>
          
          <form onSubmit={handleLogin}>
            {/* Username */}
            <div className="mb-4">
              <label htmlFor="username" className="block text-gray-700 text-sm font-semibold mb-2">Username</label>
              <input 
                type="text" 
                id="username" 
                name="username" 
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-300"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              />
            </div>

            {/* Password */}
            <div className="mb-6">
              <label htmlFor="password" className="block text-gray-700 text-sm font-semibold mb-2">Password</label>
              <input 
                type="password" 
                id="password" 
                name="password" 
                className="w-full p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-300"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>

            {/* Login Button */}
            <button type="submit" className="w-full bg-green-300 text-white py-2 rounded-2xl shadow-md hover:bg-green-400">
              Login
            </button>
          </form>
          
          {/* Forgot Password */}
          <div className="text-center mt-4">
            <a href="#" className="text-green-300 hover:text-green-400">Forgot Password?</a>
          </div>
        </div>
      </div>
      
      {/* Footer */}
      <div className="flex justify-center items-center py-4 border-t">
        <p className="text-sm text-gray-600">Don't have an account? <a href="#" className="text-green-300 hover:text-green-400">Sign up</a></p>
      </div>
    </div>
  );
}
